
<!---->
<template >
    <div>
        <!---->
        <div class="table-container">

            <!---->
            <div class="user-text">
                <img class="image" src="src/assets/img/.png" height="25" width="25"/>
                <span></span>
            </div>

            <!---->
            <input type="button" class="btn-general create-user" value=""  @click="openDialog()">

            <!---->
            <table class="table-box">
                <!---->
                <tr>
                    <th></th>
                    <th ></th>
                    <th></th>
                    <th></th>
                </tr>

                <!---->
                <tr v-for="(item,index) in user_data"> <!--v-for-->
                    <td>{{ item.user_name }}</td>
                    <td>{{item.account}}</td>
                    <td>{{item.time}}</td>
                    <td>
                        <input type="button" class="btn-general" value="" @click="openMotal(index)">    <!--v-on click index-->
                        <input type="button" class="btn-general" value="" @click="deleteUsers(index)">
                    </td>
                </tr>
            </table>
        </div>

        <!---->
        <div class="dialogs" v-bind:show.sync="show_create">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_create }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="closeDialog()"></span><!---->
                    </header>

                    <div class="dialog-body" >
                        <div class="strap">
                            <span class="span-motal"></span>
                            <input type="text"  id="text1" class="input-motal" placeholder="2-10" onfocus="this.placeholder=''" @blur="checkName('text1')"  v-model="user_name" v-bind:class="{warnBorder:this.is_active[0]}" >
                        </div>

                        <div class="strap">
                            <span class="span-motal"></span>
                            <input type="text"  id="text2" class="input-motal"  placeholder="8-10" onfocus="this.placeholder=''" @blur="checkAccount('text2')" v-model="account" v-bind:class="{warnBorder:this.is_active[1]}">
                        </div>

                        <div class="strap">
                            <span class="span-motal"></span>
                            <input type="password"  id="text3" class="input-motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkPwd('text3')" v-model="pwd" v-bind:class="{warnBorder:this.is_active[2]}">
                        </div>

                        <div>
                            <span class="span-motal"></span>
                            <input type="password" id="text4" class="input-motal" placeholder="" onfocus="this.placeholder=''" @blur="dupPwd('text3','text4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}">
                        </div>
                    </div>
                    <footer class="dialog-footer" >
                        <!---->
                        <p  class="warn-tip" v-model="tip">{{tip}}</p>
                        <!---->
                        <div>
                            <button class="btn-general" style="margin-right: 10%" @click="closeDialog()"></button>
                            <button class="btn-general "  @click="addMsg()" :disabled="add_if"></button>
                        </div>
                    </footer>
                </div>
            </div>
        </div>

        <!---->
        <div class="dialogs" v-bind:show.sync="show_change">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_change }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="closeMotal()"></span><!---->
                    </header>

                    <div class="dialog-body">
                        <div class="strap">
                            <span class="span-motal">:</span>
                            <input type="text" name="user_name" id="chan1" class="input-motal" v-model="back_account" readonly style="color: dimgrey;font-size: 15px;font-weight: 400;border-color: transparent;">
                        </div>

                        <div class="strap">
                            <span class="span-motal">:</span>
                            <input type="text"  id="chan2" class="input-motal" v-model="back_username"  @change="checkName('chan2')" v-bind:class="{warnBorder:this.is_active[0]}">
                        </div >

                        <div class="strap">
                            <span class="span-motal">:</span>
                            <input type="password"  id="chan3" class="input-motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkPwd('chan3')" v-model="pwd"  v-bind:class="{warnBorder:this.is_active[2]}" >
                        </div>

                        <div class="strap">
                            <span class="span-motal">:</span>
                            <input type="password" id="chan4" class="input-motal" placeholder="" onfocus="this.placeholder=''" @blur="dupPwd('chan3','chan4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}">
                        </div>
                    </div>

                    <footer class="dialog-footer" slot="footer">
                        <!---->
                        <span class="warn-tip" v-model="tip">{{tip}}</span>
                        <div>
                            <button class="btn-general" style="margin-right: 10%" @click="closeMotal()"></button>
                            <button class="btn-general" @click="confirmChange()" :disabled="add_if"></button>
                        </div>
                    </footer>
                </div>
            </div>
        </div>

        <!---->
        <page-component  :cur_max=this.max_page v-on:pre_page="preP" v-on:next_page="nextP" v-on:skip_page="skiP"></page-component>
    </div>
</template>


<script type="text/javascript">
    import pageComponent from "./pageComponent.vue"
    let Mock = require('mockjs');

    //
    Mock.mock('/ancient_books/get_user_list.action?page=1','get',{
        "content|20":[{
            'user_id|1-100':100,
            'name':'@FIRST',
            'account|10000000-1000000000':100000000,
            'time':'@DATETIME("yyyy-MM-dd HH:mm:ss")'
        }],
        'max_page|1-100':100
    });

    Mock.mock('/ancient_books/getToken.action','get',{
        'Token|1-100':100
    });

    export default{
        components:{ pageComponent },
        data(){
            return {
                show_create: false,
                show_change: false,

                tip: '',    //
                is_active:[false,false,false,false],    //
                add_if:true,    // / 
                check_n:true,   //
                check_a:false,   //

                user_data: [],
                user_name: '',
                account: '',
                pwd: '',
                confirm_pwd: '',
                back_index: '',
                back_username: '',
                back_account: '',
                get_url: '/ancient_books/get_user_list.action', //
                add_url: '/ancient_books/add_user.action',  //
                modify_url: '/ancient_books/modify_user.action',    //
                delete_url: '/ancient_books/delete_user_by_id.action',  //
                name_url:'/ancient_books/check_name.action',    //
                account_url:'/ancient_books/check_account.action',    //

                page: 1,//
                max_page: 1,//

                get_user: {},//
                add_user:{},//
                delete_user:{},//
                modify_user:{},//
                check_name:{}, //
                check_account:{} //
            }
        },
        created: function () {
            console.log("super_user created");
            this.getUsers(1);
            console.log("createdtoken"+this.Token);
        },
        methods: {
            // get 
            successGet(response){
                    console.log("success get users ");
                    //
                    if( response.body.content.length === 0 ){
                        console.log("");
                    }else {
                        this.max_page = response.body.max_page;
                        for ( let i = 0; i < response.body.content.length; i++ ){
                            this.user_data.push({
                                user_name: response.body.content[i].name,
                                account: response.body.content[i].account,
                                time:response.body.content[i].time,
                                user_id: response.body.content[i].user_id
                            });
                        }
                    }
            },

            failGet(){
                console.log("fail get users!");
            },

            //
            getUsers(pages){
                this.get_user.key="page";
                this.get_user.value=pages;
                this.HttpGetForm(this.get_url,this.get_user,this.successGet, this.failGet);
            },

            // post success
            successAdd(response){
                if(response.body.result===1) {
                    console.log("success create!");
                    this.cleanData();
                    this.closeDialog(); //
                }else if (response.body.result===0) {
                    console.log("fail create!");
                }
            },

            failAdd(){
                console.log("error create!");
            },

            //post
            createUsers(){
                //
                this.add_user.name = this.user_name;
                this.add_user.account = this.account;
                this.add_user.pwd = this.pwd;
                this.HttpPostForm(this.add_url, this.add_user, this.successAdd, this.failAdd);
            },

            // 
            openDialog(){
                this.show_create = true;
            },

            closeDialog() {
                this.show_create = false;
                this.user_name="";
                this.account="";
                this.pwd="";
                this.confirm_pwd="";
                this.tip="";
                this.is_active[0]=false;
                this.is_active[1]=false;
                this.is_active[2]=false;
                this.is_active[3]=false;
            },

            //
            checkName(tag1){
                document.getElementById(tag1).placeholder ="2-10";
                this.add_if = true;    //
                this.is_active[0]=false;

                let x = document.getElementById(tag1).value;
                let y;
                if( x.length !== 0){
                    y = this.checkname(x);
                    if( y === true ){
                        this.tip = "";
                        this.is_active[0]=false;
                        this.dupName(x);
                    } else {
                        this.tip = "2-10";
                        this.add_if=true;
                        this.is_active[0]=true;
                    }
                }
            },

            //
            checkAccount(tag2){
                document.getElementById(tag2).placeholder = "8-10";
                this.add_if = true;    //
                this.is_active[1]=false;

                let x = document.getElementById(tag2).value;
                let y;
                if( x.length !== 0){
                    y = this.checkuser(x);
                    if( y === true ){
                        this.tip = "";
                        this.is_active[1]=false;
                        this.dupAccount();
                    } else {
                        this.tip = "8-10";
                        this.add_if=true;
                        this.is_active[1]=true;
                    }
                }
            },


            //
            checkPwd(tag3){
                document.getElementById(tag3).placeholder = "6-16";
                this.add_if = true;    //
                this.is_active[2]=false;

                let x = document.getElementById(tag3).value;
                let y;
                if( x.length !== 0){
                    y = this.checkuser(x);
                    if( y === true ){
                        this.add_if = false;
                        this.tip = "";
                        this.is_active[2]=false;
                    } else {
                        this.tip = "6-16";
                        this.add_if=true;
                        this.is_active[2]=true;
                    }
                }
            },

            //
           /* checkIn(tag1,placeholder_text,index){
                document.getElementById(tag1).placeholder =placeholder_text;
                this.add_if = true;    //
                this.is_active[index]=false;

                let x = document.getElementById(tag1).value;
                let y;
                //
                if ( x.length !== 0 ){
                    if( tag1 === "text1" || tag1 === "chan2" ){
                        y = this.checkname(x);
                    } else if( tag1 === "text2" ){
                        y = this.checkuser(x);
                    } else if( tag1 === "text3" || tag1 === "chan3" ){
                        y =this.checkpwd(x);
                    }

                    //
                    if( y === true){
                        if(tag1 === "text1" || tag1 === "chan2"){
                            this.dupName(x);
                        } else if( tag1 === "text2" ){
                            this.dupAccount();
                        } else if( tag1 === "text3"){
                            this.add_if = false;
                        }
                        this.tip="";
                        this.is_active[index]=false;
                    } else {
                        this.tip=placeholder_text;
                        this.add_if=true;
                        this.is_active[index]=true;
                    }
                }
            },*/


            /*checkIn(tag1,placeholder_text,juge_text,index){
                let x=document.getElementById(tag1).value;
                if(x!==""){
                    if(x.match(juge_text)){
                        if(tag1 === "text1"){
                            this.dupName(x);
                        }else if( tag1 === "text2" ){
                            this.dupAccount();
                        }
                        this.tip="";
                        document.getElementById(tag1).placeholder =placeholder_text;
                        this.add_if=true;
                        this.is_active[index]=false;
                        //this.is_active[1]=false;
                        //this.is_active[2]=false;
                    }else{
                        this.tip=placeholder_text;
                        document.getElementById(tag1).placeholder = placeholder_text;
                        this.add_if=false;
                        this.is_active[index]=true;
                    }
                }else{
                    document.getElementById(tag1).placeholder =placeholder_text;
                    this.add_if=false;
                    this.is_active[index]=false;
                    //this.is_active[1]=false;
                    //this.is_active[2]=false;
                }
            }, */

            //
            dupPwd(tag1,tag2){
                document.getElementById(tag2).placeholder = "";
                this.add_if = true;
                this.is_active[3] = false;
                let x = document.getElementById(tag1).value;
                let y = document.getElementById(tag2).value;
                if(y.length !== 0){
                    if(y !== x){
                        this.tip="";
                        this.add_if = true;
                        this.is_active[3] = true;
                    }else{
                        this.tip = "";
                        this.add_if =false;
                        this.is_active[3] = false;
                    }
                }
            },

            //
            successName(response){
                if( response.body.result === 1 ){
                    console.log("");
                    this.add_if = false;    //
                } else if( response.body.result === 0 ){
                    this.is_active[0]=true;
                    this.tip="!";
                }
            },

            failName(){
              console.log("");
            },

            dupName(n){
                    this.check_name.key = "name";
                    this.check_name.value = n;
                    this.HttpGetForm(this.name_url, this.check_name, this.successName, this.failName);
            },


            //
            successAccount(response){
                if( response.body.result === 1 ){
                    console.log("");
                    this.add_if = false;
                } else if( response.body.result === 0 ){
                    this.is_active[1]=true;
                    this.tip="!";
                }
            },

            failAccount(){
                console.log("");
            },

            dupAccount(){
                    this.check_account.key = "account";
                    this.check_account.value = this.account;
                    this.HttpGetForm(this.account_url, this.check_account, this.successAccount, this.failAccount);
            },

            //
            addMsg(){
                let x = document.getElementById("text1").value;
                let y = document.getElementById("text2").value;
                let z = document.getElementById("text3").value;
                let m = document.getElementById("text4").value;
                if (x === "" || y === "" || z === "" || m === "") {
                    //this.tip = "";
                    this.add_if = true;
                } else {
                    this.checkName('text1');
                    this.checkAccount('text2');
                    this.checkPwd('text3');
                    this.dupPwd('text3','text4');
                    if ( this.add_if === false ) {
                        this.createUsers();
                    }
                }
            },
           /* addMsg(){
                let x = document.getElementById("text1").value;
                let y = document.getElementById("text2").value;
                let z = document.getElementById("text3").value;
                let m = document.getElementById("text4").value;
                if (x === "" || y === "" || z === "" || m === "") {
                    this.tip = ""
                } else {
                   this.checkIn('text1','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                   this.checkIn('text2','8-10','[a-zA-Z0-9]{8,10}$',1);
                   this.checkIn('text3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                   this.checkPwd('text3','text4');
                    if ( this.add_if === true ) {
                        this.createUsers();
                    }
                }
            },*/

            // success
            successDelete(response){
                if (response.body.result === 1) {
                    console.log("success delete");
                    this.cleanData();   //
                } else if (response.body.result === 0) {
                    console.log("fail delete");
                }
            },

            failDelete(){
                console.log("error delete");
            },

            deleteUsers(index){
                this.delete_user.user_id = this.user_data[index].user_id;
                this.HttpPostForm(this.delete_url,this.delete_user,this.successDelete,this.failDelete);
            },


            // 
            openMotal(index){
                this.show_change = true;
                this.back_index = index;    //ID
                this.back_username = this.user_data[index].user_name;
                this.back_account = this.user_data[index].account;
            },

            closeMotal(){
                this.show_change=false;
                this.back_username='';
                this.back_account='';
                this.pwd='';
                this.confirm_pwd = '';
                this.back_index='';
                this.tip='';
                this.is_active[0]=false;
                this.is_active[2]=false;
                this.is_active[3]=false;
            },

            successModify(response){
                if( response.body.result === 1 ){
                    console.log("success modify!");
                    this.cleanData();
                    this.closeCha();
                } else if( response.body.result === 0 ){
                    console.log("fail modify!");
                }
            },

            failModify(){
                console.log("error modify!");
            },

            //
            modifyUsers(){
                this.modify_user.name=this.back_username;
                this.modify_user.pwd=this.pwd;
                this.modify_user.user_id=this.user_data[this.back_index].user_id;
                this.HttpPostForm(this.modify_url,this.modify_user,this.successModify,this.failModify);
            },

            //
            confirmChange(){
                let x=document.getElementById("chan2").value;
                let y=document.getElementById("chan3").value;
                let z=document.getElementById("chan4").value;
                if (x === "" || y === "" || z === "" ) {
                    this.tip = ""
                } else {
                    this.checkIn('chan2','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                    this.checkIn('chan3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                    this.checkPwd('chan3','chan4');
                    if (this.add_if === true) {
                        this.modifyUsers();
                    }
                }
            },


            preP(){
                this.page = --this.page;
                this.cleanData();
                console.log("pre:"+this.page);
            },

            nextP(){
                this.page= ++this.page;
                this.cleanData();
                console.log("next:"+this.page);
            },

            skiP(p){
                this.page = p;
                this.cleanData();
                console.log("skip:" + this.page);
            },

            //,
            cleanData(){
                this.user_data.splice(0, this.user_data.length);
                this.getUsers(this.page);
            }


        }
    }

</script>



